@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
.social .icon{padding-left:5px;padding-right:5px}


.-about{background:url(/static/media/background.8dbf1223.jpeg);background-size:cover;background-position:center;position:relative}.-about .background{background-color:rgba(2,54,101,0.9);height:100%;position:absolute;width:100%;z-index:1}.-about h2{z-index:2}.-about .description{margin:50px auto 0;max-width:640px;text-align:center;z-index:2}

.action-button{border:2px solid #fff;color:#fff;display:block;font-weight:bold;margin-top:20px;text-align:center;text-transform:uppercase;transition:all .5s ease-in-out}.action-button:not(.-active){padding:10px;border-radius:20px}.action-button:not(.-active):hover{background-color:#fff;color:#023665;opacity:1}.action-button.-active{background-color:#fff;color:#023665;padding:20px;box-shadow:5px 5px 0 0 rgba(0,0,0,0.25);border-radius:30px}.action-button.-active:hover{opacity:.85}

.event-card{background:rgba(0,0,0,0.15);padding:20px;text-align:left}.event-card .date{font-size:12px}.event-card .description{margin-top:20px}

#error-block{background-color:rgba(0,0,0,0.15)}.newsletter-form{width:100%}@media screen and (min-width: 767px){.newsletter-form{max-width:640px}}

.footer{align-items:center;display:flex;flex-direction:column;padding:40px 0}.footer .copyright{font-size:12px;margin-top:20px}

.header{display:flex;justify-content:center;padding:20px}

.list-schedule{margin-left:20px}.list-schedule .item{list-style:disc;margin:10px 0}.list-schedule .item>p{margin:5px 0}.list-schedule .item>p>a{align-items:center;display:inline-flex}

.grid-photos{display:grid;grid-gap:10px;gap:10px;grid-template-columns:repeat(2, 1fr)}.grid-photos .card-photo{align-items:center;border-radius:10px;display:flex;height:100px;overflow:hidden}.grid-photos .photo{border-radius:10px;max-width:100%}@media screen and (min-width: 1024px){.grid-photos{grid-template-columns:repeat(4, 1fr)}.grid-photos .card-photo{height:160px}}

*{border:0;box-sizing:border-box;margin:0;outline:none;padding:0}ul,ol,li{list-style:none}body{background-image:linear-gradient(to bottom, rgba(0,85,153,0.8), #059);color:#fff;font-family:'Montserrat', sans-serif;font-size:15px;font-weight:400}a{color:#fff;text-decoration:none}a:hover{opacity:.75}h1{font-size:3rem}h2{font-size:1.5rem;line-height:30px}h3{font-size:1rem;line-height:30px}p{line-height:24px}@media screen and (min-width: 767px){h2{font-size:2rem}h3{font-size:1.5rem}}

