.container{width:90%}@media screen and (min-width: 1024px){.container{width:1020px}}

.sections{align-items:center;display:flex;flex-direction:column;justify-content:center;min-height:100vh;padding-bottom:40px;padding-top:40px;position:relative;width:100%}.sections:not(.-intern){padding-left:20px;padding-right:20px}

.list-cards{display:grid;grid-gap:10px;grid-template-columns:repeat(1, 1fr);margin-top:50px;width:100%}@media screen and (min-width: 767px){.list-cards{grid-template-columns:repeat(4, 1fr)}}

